'use strict';

/**
* Test if a value is an alphagram.
*
* @module @stdlib/assert-is-alphagram
*
* @example
* var isAlphagram = require( '@stdlib/assert-is-alphagram' );
*
* var out = isAlphagram( 'beep' );
* // returns true
*
* out = isAlphagram( 'zba' );
* // returns false
*
* out = isAlphagram( '' );
* // returns false
*/

// MODULES //

var isAlphagram = require( './main.js' );


// EXPORTS //

module.exports = isAlphagram;
